import React, {Component} from 'react';
import BigNumber from 'bignumber.js';
import numbro from "numbro";
import axios from 'axios';
import {getContracts} from './hooks/contract';

interface StateInterface {
}

class Ethereum extends Component<PropsInterface, StateInterface> {
    
    state = {account:null, project:[], projectcomp:[], projectsatoro:[]}

    async apy(){

        let ret
         const furl = 'https://api.degentoken.finance/api/v1/global.json'
         await axios({
            url: furl,
            method: 'get'
            }).then((response) => {
                   ret = response.data
                }); 
        return ret
    }

 
    async refresh_prices(token,currency) {

          let furl = 'https://api.coingecko.com/api/v3/simple/price?ids='+token+'&vs_currencies='+currency
          await axios({
              url: furl,
              method: 'get'
               }).then((response) => {
                    var data = Object.keys(response.data) ;
                    for (var i = 0; i < data.length; i++){
                        this.setState({ [data[i]+"usd"]: response.data[data[i]]["usd"]});
                        this.setState({ [data[i]+"btc"]: response.data[data[i]]["btc"]});
                        this.setState({ [data[i]+"eur"]: response.data[data[i]]["eur"]});
                        this.setState({ [data[i]+"bnb"]: response.data[data[i]]["bnb"]});
                    }
           });
    }

    async tokenPrice(pair,token,ticker) 
        {
            const pairContract =  await getContracts.PairContract(pair);
            const wethContract = await getContracts.tokenContract("0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c");
            const tokenContract  =  await getContracts.tokenContract(token);


            const tokenAmountWholeLP = await tokenContract.methods
                .balanceOf(pairContract.options.address)
                .call();

            //Number of BNB
            const lpContractWeth = await wethContract.methods
                .balanceOf(pairContract.options.address)
                .call()

            const lpWethWorth = new BigNumber(lpContractWeth)  //BNB PRICE
            const wethAmount = lpWethWorth.div(new BigNumber(tokenAmountWholeLP))
            return wethAmount
        }

 
    async refresh_lobby(account) {
        
        var wethAmount = await this.tokenPrice("0xB4771D6B484d782980Ad00CcBDA58CdcEE34BB22","0xb68a67048596502A8B88f1C10ABFF4fA99dfEc71")
        this.setState({"dgnz-bnb":wethAmount})
        this.setState({"dgnz_usd":wethAmount*this.state.binancecoinusd})

        wethAmount = await this.tokenPrice("0xBfCf2B3da134308b0F479544784CFC98415dBF85","0xe8B9b396c59A6BC136cF1f05C4D1A68A0F7C2Dd7")
        this.setState({"adgnz-bnb":wethAmount})
        this.setState({"adgnz_usd":wethAmount*this.state.binancecoinusd})

        const DGNZContract = await getContracts.DGNZContract();
        const ADGNZContract = await getContracts.ADGNZContract();

        var poolLen =  await DGNZContract.methods.poolLength().call()
        var TotalAlloc = await DGNZContract.methods.totalAllocPoint().call()
        const farms = await this.apy()

         for (let i=0 ; i< poolLen;i++){


                let project = ""
                let usdPrice = 0
                let token_price = 0
                let DGNZAmountPending = 0 
                let apyval = 0
                let retval
                let token0ContractSym
                let token1ContractSym
                let t0
                let t1
                let supply
                let wantLockedTotal
                let sharepct = 0
                let allocPoint = 0
                let expected = 0
                let expected_price = 0
                let aprfarmdaily_expected = 0
                let DGNZInfo = await DGNZContract.methods.userInfo(i,account).call();
                DGNZAmountPending = await DGNZContract.methods.pendingDGNZ(i,account).call();
                if (DGNZInfo[0] >0 && farms["farms"][i] ){
                    let interest = parseInt(DGNZAmountPending)/10**18;
                    let interest_price = interest * this.state["dgnz-bnb"]*this.state.binancecoinusd;
                    let DGNZAmount = parseInt(DGNZInfo[0])/10**18;
                    retval = farms["farms"][i]
                    apyval = retval["apy"]*365
                    token_price = retval["lpprice"]
                    token0ContractSym = retval["t0sym"]
                    token1ContractSym = retval["t1sym"]
                    t0 = retval["token0"]
                    t1 = retval["token1"]
                    supply = retval["supply"]
                    wantLockedTotal = retval["wantLockedTotal"]
                    allocPoint = retval["allocPoint"]
                    project =  retval["symbol"]
                    sharepct = DGNZAmount/wantLockedTotal;
                    expected = sharepct*(allocPoint/TotalAlloc)*(1*86400/3)
                    expected_price = expected > 0 ? expected * this.state["dgnz-bnb"]*this.state.binancecoinusd  : 0

                    usdPrice = token_price*DGNZAmount
                    if (token0ContractSym !== "")
                    {
                        let pct = DGNZAmount/supply;
                        let t0p = new BigNumber(pct*t0)
                        let t1p = new BigNumber(pct*t1)
                        project = token0ContractSym+"|"+token1ContractSym+" "+t0p.decimalPlaces(2,2).toString()+token0ContractSym+"/"+t1p.decimalPlaces(2,2).toString()+token1ContractSym
                    }
                        this.state.project.push(
                         {
                            project: project,
                            type:"Farming DGNZ",
                            apy: apyval,
                            stakedAmount: DGNZAmount,
                            sharepct: sharepct,
                            token_price: token_price,
                            interest: interest,
                            expected: expected,
                            expected_price: expected_price,
                            interest_price: interest_price,
                            aprfarmdaily_expected: aprfarmdaily_expected,
                            usdPrice: usdPrice,
                            pending: 0
                        });
                    }
        }


         poolLen =  await ADGNZContract.methods.poolLength().call()
         TotalAlloc = await ADGNZContract.methods.totalAllocPoint().call()

         for (let i=0 ; i< poolLen;i++){
            
                let project = ""
                let usdPrice = 0
                let token_price = 0
                let retval 
                let apyval = 0
                let token0ContractSym
                let token1ContractSym
                let t0
                let t1
                let supply
                let wantLockedTotal
                let sharepct = 0
                let allocPoint = 0
                let aDGNZAmountPending = 0
                let expected = 0
                let expected_price = 0
                let aprfarmdaily = 0
                let aprfarmdaily_expected = 0
                let pending = 0
                let aDGNZAmount = await ADGNZContract.methods.stakedWantTokens(i,account).call();
                aDGNZAmountPending = await ADGNZContract.methods.pendingaDGNZ(i,account).call();
                if (aDGNZAmount > 0 && farms["pools"][i]){
                    console.log(aDGNZAmount,i)
                    retval = farms["pools"][i]
                    apyval = retval["totalapy"]
                    token_price =  retval["lpprice"]
                    token0ContractSym = retval["t0sym"]
                    token1ContractSym = retval["t1sym"]
                    t0 = retval["token0"]
                    t1 = retval["token1"]
                    supply = retval["supply"]
                    wantLockedTotal = retval["wantLockedTotal"]
                    allocPoint = retval["allocPoint"]
                    aprfarmdaily = retval["aprfarmdaily"]
                    project =  retval["symbol"]
                    pending = retval["pending"]
                    let interest = parseInt(aDGNZAmountPending)/10**18;
                    let interest_price = interest * this.state["adgnz-bnb"]*this.state.binancecoinusd;
                    let DGNZAmount = aDGNZAmount/10**18;
                    sharepct = DGNZAmount/wantLockedTotal;
                    expected = sharepct*(allocPoint/TotalAlloc)*(0.01*86400/3)
                    expected_price = expected > 0 ? expected * this.state["adgnz-bnb"]*this.state.binancecoinusd  : 0

                    usdPrice = token_price*DGNZAmount
                    if (token0ContractSym !== '') {
                        let pct = DGNZAmount/supply;
                        let t0p = new BigNumber(pct*t0)
                        let t1p = new BigNumber(pct*t1)
                        project = token0ContractSym+"|"+token1ContractSym+" "+t0p.decimalPlaces(2,2).toString()+token0ContractSym+"/"+t1p.decimalPlaces(2,2).toString()+token1ContractSym
                    }
                    aprfarmdaily_expected = usdPrice*aprfarmdaily
                    if ( DGNZAmount > 0.0000001){
                        this.state.projectcomp.push(
                             {
                                project: project,
                                type:"Compounding",
                                apy: apyval,
                                stakedAmount: DGNZAmount,
                                sharepct : sharepct,
                                token_price:token_price,
                                interest: interest,
                                expected: expected,
                                expected_price: expected_price,
                                interest_price: interest_price,
                                aprfarmdaily_expected: aprfarmdaily_expected,
                                pending: pending*sharepct,
                                usdPrice: usdPrice
                            });
                        }
                    }
        }


        let totalUSD = 0;
        let totalInterest = 0;
        let totalInterest_expected = 0;
        let totalaprfarmdaily_expected = 0;

       for (let i=0 ; i< this.state.project.length;i++){
            totalUSD += this.state.project[i].usdPrice;
            totalInterest += this.state.project[i].interest_price;
            totalInterest_expected += this.state.project[i].expected_price;
        }



        for (let i=0 ; i< this.state.projectcomp.length;i++){
            totalUSD += this.state.projectcomp[i].usdPrice;
            totalInterest += this.state.projectcomp[i].interest_price;
            totalInterest_expected += this.state.projectcomp[i].expected_price;
            totalaprfarmdaily_expected += this.state.projectcomp[i].aprfarmdaily_expected;
        }
 
 
        let totalBTC = totalUSD / this.state.bitcoinusd
        let totalEUR = totalUSD / this.state.seurusd
        let totalexpected = totalInterest_expected + totalaprfarmdaily_expected

        this.setState({project:this.state.project, account:account, totalUSD:totalUSD , totalBTC:totalBTC ,totalEUR:totalEUR, totalInterest:totalInterest,totalInterest_expected:totalInterest_expected,totalaprfarmdaily_expected:totalaprfarmdaily_expected,totalexpected:totalexpected});
    }

     async componentDidMount(){
        const account = localStorage.getItem('account');
        const ids = "ethereum,bitcoin,binancecoin,binance-usd,pancakeswap-token,auto,seur"
        const currency = "usd,btc,eth,eur,bnb"
        await this.refresh_prices(ids,currency);
        this.refresh_lobby(account);
    }



    render() {
        return (
            <div id="etherum" className="eth">
                <span> 
                    <b>Crypto Prices:</b>
                <li>
                   <strong>BTC</strong>:{ numbro(this.state.bitcoinusd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>BNB</strong>:{ numbro(this.state.binancecoinusd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>DGNZ</strong>:{ numbro(this.state.dgnz_usd).format({average: false, mantissa:6, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>ADGNZ</strong>:{ numbro(this.state.adgnz_usd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                </span>
                 <span> 
                    <b>Portfolio TVL:</b>
                    <li>
                        <strong>USD:</strong> { numbro(this.state.totalUSD).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                    <li>
                        <strong>EUR:</strong> { numbro(this.state.totalEUR).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                    <li>
                        <strong>BTC:</strong> { numbro(this.state.totalBTC).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                </span>
                <span></span>
                 <span> 
                    <b>Pending (a)DGNZ|Satoro Interest:</b>
                    <li>
                        { numbro(this.state.totalInterest).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$
                    </li>
                </span>
                <span>
                    <b>Daily Expected (a)DGNZ|Satoro Interest:</b>
                    <li>
                        { numbro(this.state.totalInterest_expected).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$
                    </li>
                </span>
                <span>
                    <b>Daily AutoCompounding Expected Interest:</b>
                    <li>
                        { numbro(this.state.totalaprfarmdaily_expected).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$
                    </li>
                </span>
                <span>
                    <b>Daily Total Expected Interest:</b>
                    <li>
                        { numbro(this.state.totalexpected).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$
                    </li>
                </span>

                {this.state.project.length >0 &&
                <table class="table">
                    <caption>DGNZ:</caption>
                    <thead>
                    <tr>
                        <th>Pool</th>
                        <th>APY</th>
                        <th>Stake Amount</th>
                        <th>Pool %</th>
                        <th>LP Price</th>
                        <th>Pending Interest</th>
                        <th>Daily DGNZ Projected Rewards</th>
                        <th>USD TVL</th>
                    </tr>
                    </thead>
                    <tbody>
                    {this.state.project.map(data =>
                        <tr key={data.project}>
                            <td data-label="pool"><strong>{data.project}</strong></td>
                            <td  data-label="apy">{ numbro(data.apy).format({output:"percent",mantissa:0, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="stakeAmount">{ numbro(data.stakedAmount).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="sharepct">{ numbro(data.sharepct).format({output:"percent",mantissa:2, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="lpprice">{ numbro(data.token_price).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$</td>
                            <td data-label="PendingInterest">
                                { numbro(data.interest).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })} ({ numbro(data.interest_price).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$) 
                            </td>
                            <td data-label="DailyDGNZProjectedRewards">
                                { numbro(data.expected).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })} ({ numbro(data.expected_price).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$) 
                            </td>
                            <td data-label="USD TVL">{ numbro(data.usdPrice).formatCurrency({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}</td>
                        </tr>
                    )}
                    </tbody>
                </table>
                }

                {this.state.projectcomp.length >0 &&


                <table class="table">
                    <caption>aDGNZ Compounding:</caption>
                    <thead>
                    <tr>
                        <th>Pool</th>
                        <th>APY</th>
                        <th>Stake Amount</th>
                        <th>Pool %</th>
                        <th>LP Price</th>
                        <th>Pending Interest</th>
                        <th>Daily aDGNZ Projected Rewards</th>
                        <th>Daily AutoComp Projected Interest</th>
                        <th>Pending on Farms</th>
                        <th>USD TVL</th>
                    </tr>
                    </thead>
                    <tbody>
                    {this.state.projectcomp.map(data =>
                        <tr key={data.project}>
                            <td data-label="pool"><strong>{data.project}</strong></td>
                            <td  data-label="apy">{ numbro(data.apy).format({output:"percent",mantissa:0, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="stakeAmount">{ numbro(data.stakedAmount).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="sharepct">{ numbro(data.sharepct).format({output:"percent",mantissa:2, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="lpprice">{ numbro(data.token_price).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$</td>
                            <td data-label="PendingInterest">
                                { numbro(data.interest).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })} ({ numbro(data.interest_price).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$) 
                            </td>
                            <td data-label="DailyaDGNZProjectedRewards">
                                { numbro(data.expected).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })} ({ numbro(data.expected_price).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$) 
                            </td>
                            <td data-label="DailyProjectedAutoCompRewards">
                                { numbro(data.aprfarmdaily_expected).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$ 
                            </td>
                            <td data-label="PendingOnFarms">
                                { numbro(data.pending).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$ 
                            </td>
                            <td data-label="USD TVL">{ numbro(data.usdPrice).formatCurrency({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}</td>
                        </tr>
                    )}
                    </tbody>
                </table>
                }
 
            </div>
        );
    }
}

export default Ethereum;
