import UnlockMetamask from './components/UnlockMetamask';
import './App.css';
import '../node_modules/bulma-start/css/main.css';
import React, { Component, Fragment} from 'react';
import {HashRouter as Router, NavLink, Route, Switch} from 'react-router-dom';
import Ethereum from './Ethereum';
import Web3 from 'web3';
import Nav from './Nav';

import ReactNotification from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css';

interface PropsInterface {
}

interface StateInterface {
}

class App extends Component<PropsInterface, StateInterface> {
    private readonly eth:any;

    constructor(props:PropsInterface) {
        super(props);

        this.eth = window.ethereum;
        this.isWeb3 = true;
        this.isWeb3Locked = false;
        this.appName = 'PORTFOLIO';

          //set web3 & truffle contract
          if (typeof this.eth !== 'undefined') {  
              // Use Mist/MetaMask's provider  
              this.web3 = new Web3(Web3.givenProvider || 'https://bsc-dataseed1.binance.org');
              this.eth.enable();
              if (this.eth.coinbase === null) {
                this.isWeb3Locked = true;
                this.eth.enable();
             }
         }else{  
            this.isWeb3 = false;  
         } 

        this.state = {
            account: null,
        };

        this.eth.on('chainChanged', (_chainId) => {
            window.location.reload();
        });
        this.eth.on('accountsChanged', (accounts) => {
            this.setState({account:accounts[0]});
            localStorage.setItem('account',accounts[0]);
            window.location.reload();
        });
}

   async loadBlockChain() {
         const web3 = new Web3(Web3.givenProvider || 'https://bsc-dataseed1.binance.org')
         const network = await web3.eth.net.getNetworkType();
         const accounts = await web3.eth.getAccounts();
         this.setState({account: accounts[0], network:network});
         localStorage.setItem('account', accounts[0],'network',network);
         localStorage.setItem('network',network);
   }

    componentDidMount() {
        this.loadBlockChain()
    }
    
    render() {
     if(this.isWeb3) {
          if(this.isWeb3Locked) {
            return (
             <div>
             <UnlockMetamask message="Unlock Your Metamask/Mist Wallet" />
              </div>
             )
          }
          else{
        return (
            <Router basename="/">
                <Nav appName={this.appName} network={this.state.network} account={this.state.account} />
                <Fragment>
                    <div id="container">
                        <ReactNotification />
                        <NavLink exact to="/" activeClassName="active"></NavLink>
                        <Switch>
                        <Route exact path={'/'} render={() => <Ethereum account={this.state.account} />} />
                        </Switch>
                    </div>
                    <div id="footer">
                        Porfolio
                        <p>v0.1</p>
                    </div>
                </Fragment>
            </Router>
        )
       }
     }
    }
}

export default App;
